<template>
  <div ref="messageContainer" class="Message-container">
    <MessageArea :subTitle="`(${data.total})`" @submit="handleSubmit" title="留言板"  :list="data.rows" :isListLoading="isLoading" />
  </div>
</template>

<script>
import MessageArea from "@/components/MessageArea"
import fetchData from "@/fetchDate/fetchDate.js"
import { getMessage,postMessage } from '@/api/message.js'
import mainScroll from "@/mainScroll/mainScroll.js"
export default {
  mixins:[fetchData({ total: 0, rows: []}),mainScroll("messageContainer")],
  components:{
    MessageArea
  },
  data() {
    return{
      page:1,
      limit:10
    }
  },
  methods:{
    async fetchDatas() {
      return await getMessage(this.page,this.limit)
    },
     async handleSubmit(data,callBack) {
       let result = await postMessage(data);
       this.data.rows.unshift(result);
       this.data.total++;
        callBack("留言成功");
      },
     async getMoreMessage() {
       if (!this.hasMore) {
         return
       }
       this.isLoading = true;
       this.page++;
       let result = this.data.rows;
       let resp =  await this.fetchDatas();
       this.data.total = resp.total;
       this.data.rows = result.concat(resp.rows);
       this.isLoading = false;
      },
      handleScroll(scrollItem) {
        if (this.isLoading || !scrollItem) {
             
        return;
      }
  
        let range = 100;
        let scrollHeight = scrollItem.scrollHeight;
        let scrolltop = scrollItem.scrollTop;
        let clientHeight = scrollItem.clientHeight;
        let des = Math.abs(scrolltop + clientHeight - scrollHeight)
        if (des <= range) {
          this.getMoreMessage()
              
        }
      },

  },
  computed:{
    hasMore() {
      if(this.data.rows.length < this.data.total){
        return true
      }
      else return false
    }
  },
  created() {
        this.$bus.$on("mainScroll",this.handleScroll)
  },
  destroyed() {
        this.$bus.$off("mainScroll", this.handleScroll);
  },
}
</script>

<style scoped>
.Message-container{
  width:100%;
  height: 100%;
  overflow-y: auto;
  padding: 25px 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}
.message-area-container {
  width: 700px;
  margin: 0 auto;
}
</style>